#about-container {
    width: 100%;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /* background-color: var(--secundary-bg); */
}

.personal-info {
    width: 70%;
    min-height: calc(100vh - 300px);
    background-color: var(--primary-bg);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
}

.personal-info section {
    width: 100%;
    height: 50%;
    background-color: var(--secundary-bg);
    border-radius: 16px;
    margin: 50px 0;
}